.mountains-layer1 {
    height:208px;
    width:100%;
    background: url(../images/gory.png) repeat-x;
    position: absolute;
    bottom:120px;
    left:0;
    z-index: 2;
    animation: animMountains1 35s 0s infinite linear;
}
@keyframes animMountains1 {
    0% {background-position: 0% 0;}
    100% {background-position: -800px 0;}
}